* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.country-list {
  margin-top: 0;
  list-style: none;
  padding: 0;
}

.country-item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.country-text {
  margin: 0;
}

.country-text-card {
  font-weight: 900;
  font-size: 36;
}

span {
  font-weight: bold;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}